@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,700&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:'Roboto', sans-serif}body,html{background-color:#FBFBFD;font-size:12px}input,textarea,button{outline:none}input:focus,textarea:focus{border-bottom:1px solid black}button:focus{text-shadow:0 0 .5px black}ul{list-style:none}button{border:none;background:none}hr{border:none;height:1px}

.register{display:flex;flex-direction:column;width:85%;height:100%}.register__title{font-size:2em;letter-spacing:0.02em}.register__text{font-size:1.2em;width:60%;color:gray;font-weight:200;margin:20px 0}.register__form{display:flex;flex-direction:column;height:100%}.register__input{border-radius:10px;border:.3px solid gray;background-color:#fff;width:100%;min-height:3em;padding:5px 20px;font-size:1.3em;margin:10px 0}.register__buttons{height:80px;display:flex;justify-content:space-between;align-items:center}.register p{font-weight:700;font-size:1.2em}.register__loginButton{border:none;background:none}.register__loginText{color:gray}.register__loginTextBlue{color:#3D76FF}.register__registerButton{background-color:white;border:.5px solid #ADADAD;border-radius:50px;width:50%;padding:15px 5px;display:flex;justify-content:space-evenly;align-items:center}.register__error{color:red}

.login{display:flex;flex-direction:column;width:85%;height:100%}.login__title{font-size:2em;letter-spacing:0.02em}.login__form{margin:50px 0;display:flex;flex-direction:column;height:100%}.login__input{border-radius:10px;border:.3px solid gray;background-color:#fff;width:100%;min-height:3em;padding:5px 20px;font-size:1.3em;margin:10px 0}.login__underInputs{display:flex;justify-content:space-between;margin:2em 0}.login__forgotPassword{background:none;border:none;color:gray;font-weight:700;font-size:1.2em}.login__remember{font-size:1.2em}.login__rememberCheckbox{margin:0 10px}.login__loginButton{background-color:white;border:.5px solid #ADADAD;border-radius:50px;width:50%;padding:15px 5px;display:flex;justify-content:space-evenly;align-items:center;margin:0 auto}.login__registerButton{background:none;border:none;color:#3D76FF;font-weight:700;font-size:1.2em;margin:1em auto;padding:1em}.login__error{color:red}

.start{display:flex;flex-direction:column;align-items:center;min-height:70vh;margin:10vh 0}.start__bannerText{font-size:2.1em;width:66%;text-align:center}.start__hr{width:66%;height:1.5px;border:none;background-image:linear-gradient(to right, #2979FF, #8D5CFF, #FB3CFF);margin:2em}

.topBar{position:fixed;display:flex;align-items:center;top:0;width:100%;border-bottom:1px solid rgba(121,121,121,0.3);background-color:white;padding:10px 20px;min-height:50px}.topBar__optionsList{display:flex;flex:1 1;flex-direction:row;justify-content:space-between;align-items:center}.topBar__options--banner{font-weight:700;background:linear-gradient(to right, #2979FF, #8D5CFF, #FB3CFF);-webkit-background-clip:text;background-clip:text;color:transparent;font-size:1.2em}

.commentForm{position:fixed;bottom:0;background-color:#f3f3f3;border-radius:10px;display:flex;align-items:flex-end;padding:1em;width:100%}.commentForm__imageInput{display:none}.commentForm__textInput{align-self:center;flex:1 1;margin:0 15px;font-size:1.2em;resize:none;border:none;background-color:transparent}.commentForm__submit{font-size:1.8em;color:#4176FF}

.comment{display:flex;align-items:center;margin:1em 0;pointer-events:none}.comment__text{font-size:1.2em;max-width:75vw;overflow-wrap:break-word}.comment__sex{align-self:flex-start;margin-right:1em;height:3.5em}.comment__username{color:#4176FF;font-size:1.05em;height:3.5em;font-weight:700}

.comments{width:100vw;height:100vh;position:fixed;top:0;left:0;background-color:#FBFBFD;overflow-y:scroll;padding-bottom:15vh;z-index:2}.comments__topBar{position:fixed;display:flex;align-items:center;top:0;width:100%;border-bottom:1px solid rgba(121,121,121,0.3);background-color:white;padding:10px 20px;min-height:50px;max-height:60px}.comments__title{margin:0 1em;font-size:1.3em;font-weight:700}.comments__backButtonImg{width:1.4em}.comments__list{margin-top:65px;padding:0 1em}

.post{border:.5px solid #DFDFDF;border-radius:5px;padding:2%;display:flex;align-items:flex-start;justify-content:center;flex-wrap:wrap;margin:1em 0}.post__sex{width:14%}.post__content{margin:3% 2%;display:flex;flex-basis:75%;flex-direction:column}.post__username{font-size:1.1em;font-weight:700;color:#4176FF}.post__postDate{color:gray;font-size:.9em}.post__reportButton{flex-basis:5%}.post__body{font-size:1.2em;margin:.5em 0;max-width:70vw;overflow-wrap:break-word}.post__image{width:100%;border:2px solid #4176FF;border-radius:10px;max-height:500px;object-fit:cover}.post__buttons{margin:15px 0 5px 0;display:flex;width:100%}.post__like{color:#5B5B5B;font-weight:700;font-size:1.3em}.post__dislike{color:#5B5B5B;font-weight:700;margin:0 10px;font-size:1.3em}.post__commentsButton{margin:0 0 0 auto;color:#5B5B5B;font-weight:700}.post__bottom{flex-basis:100%}.post__hr{background-color:#DFDFDF}.post__votes{margin-top:.8em}.post__reactions{font-weight:700}.post__comments{margin-top:1em;max-width:85vw;overflow-wrap:break-word}.post__showAllButton{color:gray;margin:.5em 0}


.createPostForm{background-color:#eff0f4;border:1px solid rgba(128,128,128,0.4);border-radius:10px;position:fixed;width:95%;top:10%;left:2.5%}.createPostForm__handle{display:flex;justify-content:space-between;padding:1em 0.8em;border-bottom:1px solid rgba(128,128,128,0.4)}.createPostForm__closeButton{border:none;position:relative;z-index:1;background-color:transparent}.createPostForm__form{display:flex;flex-direction:column;padding:0 2em}.createPostForm__formIco{margin-right:0.5em}.createPostForm__handleTitle{font-size:1.2em}.createPostForm__submit{background-color:#2979ff;font-weight:700;color:white;padding:0.8em 0;width:40%;border-radius:20px;margin:0 0 1em auto}.createPostForm__postBody{border-radius:10px;padding:1em;resize:none}.createPostForm__label{font-weight:700;font-size:1.2em;margin:1em 0}.createPostForm__imageButton{display:none}.createPostForm__labelButtonStyle{width:50%}

.createPost{width:100%;height:90%}.createPost__button{border:none;background:none;position:fixed;right:15px;bottom:15px}.createPost__button img{width:55px}

.chan{margin:100px 5px}

.topBarChat{width:100%;display:flex;position:fixed;z-index:1;background-color:#fff;justify-content:space-between;align-items:center;padding:10px;border-bottom:1px solid rgba(121,121,121,0.3)}.topBarChat__backButton{flex:1 1}.topBarChat__title{font-size:1.5em;flex:100% 1;text-align:center}.topBarChat__online{display:flex;flex:1 1}.topBarChat__onlineNumber{margin-right:.5em;color:#3078FF;font-weight:700;font-size:1.2em}

.message{max-width:70%;display:flex;align-items:center;margin:2em 0}.message--end{flex-flow:row-reverse;margin-left:auto}.message:last-child{margin-bottom:2em}.message:first-child{margin-top:5em}.message__speechBubble{border:0.5px solid rgba(128,128,128,0.472);border-radius:20px;padding:1em;font-size:1.2em;overflow-wrap:break-word;max-width:100%}.message__speechBubble--end{background-color:#3078FF;color:white}.message__username{margin:0 1em;color:gray}

.messages{width:95%;padding-bottom:1%;overflow:hidden;flex:auto;border-bottom:.3px solid black}.messages__scroll{overflow:scroll;height:100%}.messages__scrollButton{background-color:rgba(48,120,255,0.5);border:1px solid rgba(0,0,0,0.2)}.messages__loadMoreButton{margin:50px 0}.react-auto-scroll__option{opacity:0}

.messageForm{width:95%;display:flex;position:relative;margin:1em 0;justify-content:center;align-items:flex-end;border:0.5px solid gray;border-radius:20px;padding:5px 10px;background-color:#fff}.messageForm__textarea{margin:0 10px;width:80%;max-height:6em;align-self:center;resize:none;border:none;background:transparent}.messageForm__sendImageButton img{height:2.2em}.messageForm__sendMessageButton img{height:2.2em}

.conversation{width:100vw;height:100vh;position:absolute;top:0;background-color:#fff;display:flex;flex-direction:column;align-items:center;overflow:hidden}

.chat{padding:80px 30px;width:100%;height:100vh}.chat__all{background-image:linear-gradient(to left, #2979FF, #8D5CFF, #FB3CFF);width:100%;display:flex;align-items:center;justify-content:space-between;color:white;border-radius:15px;font-size:1.2em;font-weight:700;padding:10px}.chat__allHeader{display:flex;align-items:center}.chat__allOnline{display:flex}.chat__allTitle{margin:0 15px}.chat__allOnlineNumber{margin:0 15px}

.menu{height:100%;width:100%;position:fixed;top:0}.menu__bg{width:100%;height:100%;background-color:rgba(0,0,0,0.5);float:right}.menu__content{position:absolute;height:100%;width:60%;background-color:#f5f5f5}.menu__close{position:absolute;right:20px}.menu__header{margin:15% 0 25% 20px;font-size:1.5em;display:flex;align-items:center}.menu__profile{margin-right:1em}.menu__username{color:#4176FF;font-weight:700}.menu__option{margin:10% 20px;padding:5% 0;border-bottom:1px solid rgba(128,128,128,0.246);font-size:1.4em}.menu__option--chosen{font-weight:700}.menu__option--logout{color:red}

